import avatorGroup from "./avator-group.vue"
export default {
	name: 'wkiwi-swipe-action',
	props: {
		options: Array,
		messagesList:Array,
	},
	components: {avatorGroup},
	data() {
		const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			elId: elId,
			transformX: 'translateX(0px)',
			messageIndex: -1,
			showdelete: 0,
		}
	},
	created() {
		this.direction = ''
		this.startX = 0
		this.startY = 0
		this.btnGroupWidth = 0
		this.isMoving = false
	},






	onReady() {
	},

	methods: {
		toMessageDetail(i,type){
			if(type == 2){
				console.log('进入到聊天界面'+i)
				uni.navigateTo({
					url:"../message_info/message_info"
				})
			}else if(type == 1){
				console.log('进入系统消息界面'+i)
			}
		},
		bindClickBtn(item, index) {
			this.messageIndex = -1;
			console.log(item.text +'message第'+ index+ '项');
			
		},
		touchStart(event) {
			if(event.currentTarget.dataset.disabled === true){
				return;
			}
			this.startX = event.touches[0].pageX;
			this.startY = event.touches[0].pageY;
		},
		touchMove(event) {
			if (this.direction === 'Y' || event.currentTarget.dataset.disabled === true) {
				this.direction = '';
				return;
			}
			var moveY = event.touches[0].pageY - this.startY,
			//  moveX用于判断方向
				moveX = -(event.touches[0].pageX - this.startX);
			if (!this.isMoving && Math.abs(moveY) > Math.abs(moveX) || Math.abs(moveY) > 100 || Math.abs(moveX) < 50) { //纵向滑动//参数100与50可调节侧滑灵敏度
				this.direction = 'Y';
				return;
			}
			// 移动距离
			console.log(moveX);
			this.direction = moveX > 0 ? 'right' : 'left';
			// 输出方向
			console.log(this.direction);
			this.messageIndex = moveX < 0 ? event.currentTarget.dataset.index : -1;
			this.isMoving = true;
		},
		touchEnd(event) {
			this.isMoving = false;
			if (this.direction !== 'right' && this.direction !== 'left') {
				this.direction = '';
				return;
			}
			
			// right -> left
			if (this.direction == 'right') {
				this.messageIndex = -1;
			} 
			console.log(this.messageIndex);
			this.endMove(event)
		},
		endMove(event) {
			if (this.direction === 'Y' || event.currentTarget.dataset.disabled === true) {
				this.direction = '';
				return;
			}
			if (this.messageIndex !== -1) {
				// 负号控制块左右移动
				// this.transformX = `translateX(${-this.btnGroupWidth}px)`;
				this.transformX = `translateX(${58}px)`;
				this.$emit('opened');
				this.showdelete = 1;
			} else {
				this.transformX = 'translateX(0px)';
				this.$emit('closed');
				this.showdelete = 0;
			}
			this.direction = '';
		}
	}
}
